import links from '../data/links'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { fab } from '@fortawesome/free-brands-svg-icons'

const LinkButton = ({text, url, fontAwesome}) => {
    return <li>
         <a href={url}>
            {fontAwesome ? <FontAwesomeIcon icon={[fab, fontAwesome]} /> : text}
         </a>
    </li>
}

const LinkButtons = () => {
    return <ul>
        {links.map(link => <LinkButton key={link.text} {...link}/>)}
    </ul>
}

export default LinkButtons